        id: "",
      },
      old_href: "", // 原人脸图片
      change_href: "",
      hasNewImage: false,
      new_file: "",
      student: "", // 搜索--学号
      type: "", // 选中的搜索方式
      typeList: [
        {
          // 搜索方式
          value: 1,
          label: "按学生学号搜索",
        },
