      logsCurrent: 1,
      logsSize: 10,
      logsTotal: 0,
      log_id: "",

      tableData: [],
      face_image: "",
      // 分页
      current: 1,
      total: 0,
      size: 10,
      dialogPushFace: false,
      dialogForbidden: false,
      forbidden_id: "",
      openFace_id: "",

      dialogCreateFace: false,
      dialogOpenedServe: false,
      serviceList: [],
      keyword: "",
      uuid: "",
      hasUuid: false,
      hasFace: false, // 存在人脸图片以及人脸id

      permissions: localStorage.getItem("permissions"),
      role: localStorage.getItem("username"),
    };
  },
  mounted() {
    this.getStudent();
    this.getGrade();
    // this.getFamily();
    this.getQiniuToken();
  },
  methods: {
    // 表格数据
    getStudent() {
      var self = this;
      API.students(self.current)
        .then((res) => {
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    // 分页
    currentChange(val) {
      var self = this;
      self.current = val;
      self.loading = true;
      if (self.student) {
        switch (self.type) {
          case 1:
            var number = self.student;
            API.searchName(val, self.size, name, number)
              .then((res) => {
                self.loading = false;
                self.$message.success("搜索成功！");
                self.tableData = res.data;
                self.total = res.total;
              })
              .catch((err) => {
                self.loading = false;
              });
            break;
          case 2:
            var name = self.student;
            API.searchName(val, self.size, name, number)
              .then((res) => {
                self.loading = false;
                self.$message.success("搜索成功！");
                self.tableData = res.data;
                self.total = res.total;
              })
              .catch((err) => {
                self.loading = false;
              });
        }
      } else {
        API.students(val, self.size)
          .then((res) => {
            self.loading = false;
            self.tableData = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      }
    },
    sizeChange(val) {
      var self = this;
      self.size = val;
      self.loading = true;
      if (self.student) {
        switch (self.type) {
          case 1:
            var number = self.student;
            API.searchNumber(self.current, val, number)
              .then((res) => {
                self.loading = false;
                self.tableData = res.data;
                self.total = res.total;
                self.$message.success("搜索成功！");
              })
              .catch((err) => {
                self.loading = false;
              });
            break;
          case 2:
            var name = self.student;
            API.searchName(self.current, val, name)
              .then((res) => {
                self.loading = false;
                self.tableData = res.data;
                self.total = res.total;
                self.$message.success("搜索成功！");
              })
              .catch((err) => {
                self.loading = false;
              });
        }
      } else {
        API.students(self.current, val)
          .then((res) => {
            self.loading = false;

            self.tableData = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      }
    },

    // 选择搜索方式
    changeType(val) {
      var self = this;
      self.typeDisabled = true;
      self.student = "";
    },
    search() {
      var self = this;
      self.loading = true;
      if (!self.type) {
        self.$message.error("请先选择搜索方式");
        self.loading = false;
      } else {
        if (self.type == 1) {
          self.current = 1;
          var number = self.student;
          API.searchName(self.current, self.size, name, number).then((res) => {
            self.loading = false;
            self.$message.success("搜索成功！");
            self.tableData = res.data;
            self.total = res.total;
          });
        }
        if (self.type == 2) {
          var name = self.student;
          self.current = 1;
          API.searchName(self.current, self.size, name, number).then((res) => {
            self.loading = false;
            self.$message.success("搜索成功！");
            self.tableData = res.data;
            self.total = res.total;
          });
        }
      }
    },

    // 获取年级
    getGrade() {
      var self = this;
      API.grades(self.current).then((res) => {
        self.gradeList = res.data;
      });
    },
    // 获取学生列表
    refresh() {
      var self = this;
      self.size = 10;
      API.students(1).then((res) => {
        self.tableData = res.data;
        self.total = res.total;
        self.current = 1;
      });
    },
    getClass(grade_id) {
      var self = this;
      API.classes(1, 1000, grade_id).then((res) => {
        self.classList = res.data;
      });
    },
    // 年级选中
    gradeOnChange(value) {
      var self = this;
      self.grade = value;
      self.getClass(value);
      self.form.grade_id = value;
    },
    // 班级选中
    classOnChange(value) {
      var self = this;
      self.form.class_id = value;
    },
    addStudent() {
      var self = this;
      self.dialogStudent = true;
      self.grade = "";
      self.classValue = "";
      self.form = {
        name: "",
        number: "",
        sex: 1,
        age: "",
        class_id: "",
      };
    },
    newStudent() {
      var self = this;
      if (
        self.form.name &&
        self.form.number &&
        self.form.age &&
        self.grade &&
        self.form.class_id &&
        self.form.age != 0
      ) {
        API.student(self.form).then((res) => {
          self.$message.success("提交成功");
          self.getStudent();
          self.dialogStudent = false;
          self.grade = "";
          self.classValue = "";
          self.form = {
            name: "",
            number: "",
            sex: 1,
            age: "",
            class_id: "",
          };
          self.current = 1;
        });
      } else {
        self.$message.warning("请填写正确信息");
      }
    },

    // 操作
    // handleStudentInfo(index, row) {
    //   var self = this;
    //   console.log(row);
    //   self.stuInfo.name = row.name;
    //   self.stuInfo.number = row.number;
    //   self.dialogStuInfo = true;
    //   API.stuInfo(self.stuInfo).then(res => {
    //     console.log(res);
    //     self.studentList = res.accreditl
    //     self.tableList = res
    //     console.log(res.accreditl);
    //   })
    // },
    // 绑定家长
    handleFamily(index, row) {
      var self = this;
      console.log(row);
      self.bindFormFamily.student_id = row.id;
      self.dialogFamily = true;
      self.familyList = [];
    },
    // 搜索家长
    searchFamily(keyword) {
      var self = this;
      console.log(self.keyword);
      API.seekUser(self.keyword).then((res) => {
        console.log(res);
        self.familyList = res;
      });
    },
    // 提交绑定家长
    bindFamily(index, row) {
      var self = this;
      console.log(row);
      self.bindFormFamily.user_id = row.user_id;
      console.log(self.bindFormFamily);
      API.bindFamily(self.bindFormFamily).then((res) => {
        self.$message.success("绑定成功");
        self.current = 1;
        self.getStudent();
        self.bindFormFamily.user_id = "";
        self.dialogFamily = false;
        self.familyList = [];
        self.keyword = "";
      });
    },
    // 更换人脸
    handleFace(index, row) {
      var self = this;
      self.dialogFace = true;
      self.familyForm.id = row.id;
      self.familyForm.only_in = row.only_in;
      self.old_href = row.face_image;
    },

    // 禁用人脸
    handleForbidden(index, row) {
      var self = this;
      console.log(row);
      self.forbidden_id = row.id;
      self.dialogForbidden = true;
      if (row.face_id && row.face_image) {
        self.hasFace = true;
      } else {
        self.hasFace = false;
      }
    },
    forbiddenFace() {
      var self = this;
      var student = "student";
      if (self.hasFace) {
        const loading = self.$loading({
          lock: true,
          text: "禁用中...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });
        API.failFace(self.forbidden_id, student)
          .then((res) => {
            self.dialogForbidden = false;
            loading.close();
            self.$message.success("禁用成功");
          })
          .catch((err) => {
            loading.close();
          });
      } else {
        self.$message.error("没有人脸, 无法禁用!");
      }
    },

    // 推送人脸
    handlePushFace(index, row) {
      var self = this;
      console.log(row);
      if (row.school) {
        if (row.school.offline == 1) {
          self.hasUuid = true;
        }
      }
      self.dialogPushFace = true;
      self.openFace_id = row.id;
      if (row.face_id && row.face_image) {
        self.hasFace = true;
      } else {
        self.hasFace = false;
      }
    },
    inputUuid(value) {
      console.log(value);
      var self = this;
      self.uuid = value;
    },
    pushFace() {
      var self = this;
      var student = "student";

      if (self.uuid) {
        if (self.hasFace) {
          const loading = self.$loading({
            lock: true,
            text: "推送中...",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)",
          });
          API.pushFace1(self.openFace_id, student, self.uuid)
            .then((res) => {
              self.dialogPushFace = false;
              self.uuid = "";
              loading.close();
              self.$message.success("推送成功");
            })
            .catch((err) => {
              loading.close();
            });
        } else {
          self.$message.error("没有人脸, 无法推送!");
        }
      } else {
        if (self.hasFace) {
          const loading = self.$loading({
            lock: true,
            text: "推送中...",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.7)",
          });
          API.pushFace(self.openFace_id, student)
            .then((res) => {
              self.dialogPushFace = false;
              loading.close();
              self.$message.success("推送成功");
            })
            .catch((err) => {
              loading.close();
            });
        } else {
          self.$message.error("没有人脸, 无法推送!");
        }
      }
    },

    // 创建人脸关系
    handleCreateFace(index, row) {
      var self = this;
      self.id = row.id;
      self.dialogCreateFace = true;
      console.log(row);
      if (row.face_id && row.face_image) {
        self.hasFace = true;
      } else {
        self.hasFace = false;
      }
    },
    createFace() {
      var self = this;
      if (self.hasFace) {
        const loading = self.$loading({
          lock: true,
          text: "创建中...",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)",
        });
        API.createFace(self.id)
          .then((res) => {
            self.dialogCreateFace = false;
            loading.close();
            self.$message.success("创建成功");
          })
          .catch((err) => {
            loading.close();
          });
      } else {
        self.$message.error("没有人脸, 无法创建人脸关系!");
      }
    },

    // 已开通服务
    handleServe(index, row) {
      var self = this;
      self.id = row.id;
      self.dialogOpenedServe = true;
      API.studentServe(self.id).then((res) => {
        console.log(res);
        self.$message.success("获取数据成功");
        self.serviceList = res;
      });
    },

    // 编辑学生
    handleEdit(index, row) {
      var self = this;
      // self.getStudent();
      self.dialogStudent = true;
      row.sex = Number(row.sex);
      self.form = row;
      self.grade = row.grade_id;
      self.classValue = row.class;
    },

    // 删除
    handleDel(index, row) {
      var self = this;
      self.dialogDel = true;
      self.id = row.id;
    },
    toDel() {
      var self = this;
      API.delStudent(self.id).then((res) => {
        self.$message.success("删除成功");
        self.dialogDel = false;
        self.getStudent();
        self.current = 1;
      });
    },

    // 进出记录
    handleFaceLogs(index, row) {
      var self = this;
      self.dialogFaceLogs = true;
      self.log_id = row.id;
      self.logsCurrent = 1;
      self.logsSize = 10;
      API.faceLogs(self.logsCurrent, self.logsSize, self.log_id).then((res) => {
        self.$message.success("获取数据成功!");
        self.FaceLogsDate = res.data;
        self.logsTotal = res.total;
        self.FaceLogsDate.forEach((item) => {
          item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
        });
      });
    },
    // 分页
    logsCurrentChange(val) {
      var self = this;
      self.logsCurrent = val;
      API.faceLogs(val, self.logsSize, self.log_id).then((res) => {
        self.FaceLogsDate = res.data;
        self.logsTotal = res.total;
        self.FaceLogsDate.forEach((item) => {
          item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
        });
      });
    },
    logSizeChange(val) {
      var self = this;
      self.logsSize = val;
      API.faceLogs(self.logsCurrent, val, self.log_id).then((res) => {
        self.FaceLogsDate = res.data;
        self.logsTotal = res.total;
        self.FaceLogsDate.forEach((item) => {
          item.timestamp = DATE.formatTime(item.timestamp, "Y-M-D h:m:s");
        });
      });
    },

    // 人脸信息
    handleChange(file) {
      var self = this;
      self.change_href = URL.createObjectURL(file.raw);
      self.hasNewImage = true;
    },
    handleRemove(file) {
      var self = this;
      self.change_href = "";
      self.hasNewImage = false;
    },
    beforeAvatarUpload(file) {
      var self = this;
      // self.familyForm.href = file.name;
      self.fileName = md5(file.name);
      self.suffix = file.name.substring(file.name.lastIndexOf(".") + 1);
      self.imgData.key = `tmp_${self.fileName}.${self.suffix}`;
    },
    changeFace() {
      var self = this;
      if (self.change_href === "") {
        self.familyForm.href = self.old_href;
        API.studentFace(self.familyForm).then((res) => {
          self.$message.success("上传成功");
          self.current = 1;
          self.getStudent();
          self.familyForm.href = "";
          self.dialogFace = false;
        });
      } else {
        this.$refs.upload.submit();
      }
    },
    handleAvatarSuccess(res, file) {
      var self = this;
      file.url = `${self.qiniuaddr}/${res.key}`;
      self.familyForm.href = file.url;
      API.studentFace(self.familyForm).then((res) => {
        self.$message.success("上传成功");
        self.current = 1;
        self.getStudent();
        self.$refs.upload.clearFiles();
        self.familyForm.href = "";
        self.change_href = "";
        self.old_href = "";
        self.imgData.key = "";
        self.familyForm.user_id = "";
        self.dialogFace = false;
      });
    },
    handleExceed(file, fileList) {
      //图片上传超过数量限制
      var self = this;
      self.$message.error("上传图片不能超过1张!重新上传");
      self.$refs.upload.clearFiles();
      self.familyForm.href = "";
      self.imgData.key = "";
      self.familyForm.user_id = "";
    },
    getQiniuToken() {
      var self = this;
      axios
        .get("https://api.fengniaotuangou.cn/api/upload/token")
        .then((res) => {
          self.imgData.token = res.data.uptoken;
        });
    },
  },
};
</script>

